@import './style';

.bet-modal{

    position: absolute;
    z-index: 10;
    width: 100%;
    height: 100%;    
    color: var(--bet-modal-font-color);

    .content{
        
        position: absolute;
        z-index: 1;

        width: 80%;
        height: 50%;

        min-height: 350px;

        margin-top: 25%;
        margin-left: 10%;

        display: flex;
        flex-direction: column;
        align-items: center;


        background: var(--bet-modal-background);
        box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.75);
        border-radius: 12px;


        /*@media screen and (min-width: $desktop-size) {
            width: 30%;
            margin-top: 10%;
            margin-left: 35%;            
        }*/

        h2{
            color: var(--bet-modal-font-color);
            @extend .base-roboto;
            font-weight: bold;
            margin-top: 20px;

            /*@media screen and (min-width: $desktop-size) {
                margin-top: 50px;         
                font-size: 20px;   
            }*/
        }

        .bet-value{
            margin-top: 40px;
            font-size: 40px;
        }

        .bet-buttons{
            margin-top: 10px;
            width: 70%;

            display: grid;
            grid-template-rows: 50% 50%;
            grid-template-columns: 50% 50%;
            align-items: center;
            justify-items: center;

            .bet-button{

                margin-top: 20px;
                width: 80%;
                height: 50%;

                cursor: pointer;
                background: var(--bet-modal-button-background);
                border-radius: 24px;
                text-align: center;

                color: var(--bet-modal-button-font);

                padding: 5px;
            }

            .bet-button-selected{
                @extend .bet-button;
                color:var(--bet-modal-button-font-selected);
                background: var(--bet-modal-button-background-selected);
            }

          
        }
        .bet-confirm{
            margin-top: 10px;
            font-size: 24px;
            border-bottom: 4px solid #F3F4F3;
            cursor: pointer;
        }

        .custom-value-container{

            display: flex;
            justify-content: center;

            input {
                background: none;
                border: none;
                border-bottom: 2px solid #F3F4F3;
            }
            input:focus {
                background: none;
                border: none;
                border-bottom: 2px solid #F3F4F3;
            }

            margin-top: 20px;
            overflow: hidden;
            height: 0px;
            .custom-value{
                
                width: 70%;
                text-align: center;
                background: transparent;
                border-bottom: 2px solid #F3F4F3;
                outline: none;
                font-size: 20px;
                color: var(--bet-modal-font-color);
    
            }

        }

    }
    .background{
        width: 100%;
        height: 100vh;
        background: #0F1724;
        opacity: 0%;
    }
}

