@import './style';

.pool-size{

    position: absolute;
    z-index: 4;
    width: 60%;
    height: 30px;
    bottom: 7%;

    display: flex;
    align-items: center;
    justify-content: center;

    color: var(--pool-font-color);

    @media screen and (max-height: $min-height) {
        bottom: 2%;
    }

    
    .pool-size-background{
        @extend .base-sora;

        position: absolute;
        width: 100%;
        height: 100%;
        background: var(--pool-size-background);

        transform: skew(-30deg);

      

    }

    .pool-size-content{
        position: absolute;
        z-index: 2;
    }

   
}



.pool-size-desktop{

    position: absolute;
    z-index: 4;
    height: 30px;

    display: flex;
    align-items: center;
    justify-content: center;

    color: var(--pool-font-color);

    @media screen and (max-height: $min-height) {
        bottom: 2%;
    }

    
    width: 600px;
    bottom: 10%;

    left:  calc(50% - 300px);

    .pool-size-background{
        @extend .base-sora;

        position: absolute;
        width: 100%;
        height: 100%;
        background: var(--pool-size-background);

        transform: skew(-30deg);

            font-size: 50px;

    }

    .pool-size-content{
        position: absolute;
        z-index: 2;

            font-size: 30px;
    }

   
}