@import './style';

.play-area{
    
    overflow: hidden;

    position: absolute;
    bottom: 0px;

    width: 100%;
    height: 50%;

    .play-area-buttons{

        overflow: hidden;
        
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: row;
        
        .play-area-button{
            width: 100%;
            height: 100%;

            position: relative;

            .button-content
            {
                width: 100%;
                height: 40%;
                margin-top: 70%;
                position: absolute;
                z-index: 2;
                text-align: center;                
            }

            .svg-icon{
                pointer-events: none;
            }

            .svg-text-header{
                @extend .base-sora;
                color: var(--buttons-font-color);                
            }
            .svg-text
            {
               @extend .base-sora;
               color: var(--buttons-font-color);
            }
        }
    
        .play-area-button-left{
            @extend .play-area-button;
            margin-top: 10px;
            
            .play-button-background-left{

                margin-left: -50%;
                width: 150%;
                height: 100%;
                background:var(--primary-color);
                transform: skew(-10deg);

            }
          
        }
    
        .play-area-button-right{
            margin-left:0px;
            @extend .play-area-button;

            .play-button-background-right{

                width: 150%;
                height: 100%;
                background:var(--secondary-color);
                transform: skew(-10deg);

            }
            
            
        }
    }
}
