.demo-container-menu{

    position: absolute;
    z-index: 5;

    overflow: hidden;

    width: 70px;
    height: 400px;

    background: #141C29;

    .content{

        width: 70px;
        height: 400px;

        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-direction: column;

        .demo-container-menu-button{
            width: 50%;
        }

        .demo-container-menu-last{
            display: grid;
            grid-template-columns: 50% 50%;
            justify-items: center;
            align-items: center;
            .demo-container-menu-home{
                width: 60%;
            }
    
            .demo-container-menu-scale{
                width: 60%;
            }
        }

       
    }
    

  
    
}