@import './style';
#toast {
  position: fixed;
  bottom: 10px;
  right: 10px;
  box-shadow: 0 0 0;
  padding: 20px;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  opacity: 0;
  .toast__wrapper {
    position: relative;
    display: flex;
    .toast__close {
      position: absolute;
      top: -10px;
      right: -10px;
      cursor: pointer;
      >svg {
        font-size: 16px;
      }
    }
    .toast__images {
      width: 45px;
      max-width: 45px;
      flex: 0 0 45px;
      > svg {
        color: #fff;
        font-size: 30px;
      }
    }
    .toast__content {
      width: 220px;
      max-width: 220px;
      flex: 0 0 220px;
      color: #fff;
      .toast__title {
        text-transform: capitalize;
        font-size: 18px;
        margin-bottom: 10px;
      }
      .toast__message {
        font-size: 14px;
      }
    }
  }
}