.demo-container{
    width: 100%;
    height: 100%;

    .play-container{

        position: absolute;
        z-index: 2;
        width: 488px;
        height: 892px;

        margin-top: 105px;

        background: var(--background-color);

        overflow: hidden;

        transform-origin: left top;
        transform: scale(0.6);

        margin-left: calc(50% - 202px);   
    }

    .container-background{
        position: absolute;
        width: 700px;
        height: 900px;

        margin-left: calc(50% - 268px);  
        margin-top: 0px;
        img{
            width: 700px;
        }
    }

    
}