@import './style';
.bet-value-menu{

    position: absolute;

   
    z-index: 1;

    width: 120px;
    height: 40px;

    cursor: pointer;
    
    background: var(--bet-button-background-color);
    border: 2px solid var(--bet-button-border-color);
    border-radius: 12px;

    display: flex;
    align-items: center;
    justify-content: center;

    color: var(--bet-button-modal-font-color);

    left:  calc(50% - 60px);

    top: calc(50% - 10px);   

    //@media screen and (min-width: $desktop-size) {
    //    top: calc(46% - 10px);   
    //}
}

.bet-value-menu-desktop{

    position: absolute;

   
    z-index: 1;

    width: 120px;
    height: 40px;

    cursor: pointer;
    
    background: var(--bet-button-background-color);
    border: 2px solid var(--bet-button-border-color);
    border-radius: 12px;

    display: flex;
    align-items: center;
    justify-content: center;

    color: var(--bet-button-modal-font-color);

    left:  calc(50% - 60px);
    top: calc(46% - 10px);   
}