@import './style';

.bt-game-desktop{
    @extend .base-sora;
    
    width: 100%;    
    height: 100%;    
    color: var(--buttons-font-color);
    cursor: pointer;

    .bt-game-desktop-background{
        width: 100%;
        height: 100%;        
    }
    .bt-game-desktop-content{
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: 3;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

    }
    .bt-game-desktop-icon{
        width:50px;
    }
}

.bt-game-desktop-left{
    @extend .bt-game-desktop;
    margin-top: 10px;
}

.bt-game-desktop-right{
    @extend .bt-game-desktop;
    
}
