.demo-panel{

    width: 100%;
    height: 100%;

    overflow: hidden;

    display: grid;
    grid-template-rows: 30% 70%;
    
    justify-items: center;
    align-items: center;

    background-image: url("./assets/images/demo_bg.png");
    background-size: cover;
 
    text-align: center;

    .demo-buttons{

        margin-left: 15%;
         width: 100%;
         display: grid;
         grid-template-columns: 20% 20% 20% 20% 20%;
        .device{
            height: 600px;
            cursor: pointer;
        }
    }

    .logo-demo{
        width: 350px;
    }
   
}