@import './style';

.play-mobile{
    width: 100%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    flex-direction: column;

    align-items: center;    
    justify-items: center;

}

.confetti-container{
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 50;
    pointer-events: none;
}

.play-desktop{
    width: 100%;
    height: 100%;

    margin-left: 0%;

    display: grid;
    grid-template-rows: 20% 25% auto;

    justify-items: center;

    @media (max-width: 1280px) and (max-height: 780px) {
        grid-template-rows: 15% 30% auto;
    }

    .header-container{
        
        position: absolute;
        z-index: 1;
        
        width: 90%;
        height: 15%;
        top: 0px;
        left: 5%;

        background: #141C29;

        display: flex;
        justify-content:space-around;

        @media screen and (min-width: $desktop-size) {
            background: var(--desktop-header-background-color);
        }

    }
}