@import './style';

.bet-confirmation
{
    display: grid;
    justify-content: center;
    align-content: center;

    position:absolute;
    width: 100%;
    height: 100vh;
    z-index: 3;

    /*@media screen and (min-width: $desktop-size) {
        background: rgba(15, 23, 36 , 0.95);
    }*/

    .bet-confirmation-card{

        width: 100%;
        height: 100vh;

        display: grid;
        grid-template-rows: 15% 45% 5% 5%;
        justify-items: center;
        align-items: center;

        

        @media screen and (max-height: $min-height) {
            grid-template-rows: 15% 35% 5% 20%;
        }

        /*@media screen and (min-width: $desktop-size) {
            width: 375px;
            height: 570px;
            border-radius: 10px;

            grid-template-rows: 15% 45% 5% 5%;
        }*/
    }

    .header{

        color: var(--card-header-font-color);

        @extend .base-sora;
        font-size: 36px;

        @media screen and (max-height: $min-height) {
            font-size: 28px;
        }
    
    }

    .confirmation-card
    {
        width: 90%;
        height: 100%;


        display: grid;
        grid-template-rows: 20% 50% 30%;
        
        .card-header{
            
            background-image: url("../assets/images/card-top.svg");
            background-size: cover;
            background-repeat: no-repeat;
            

            display: grid;
            grid-template-columns: 50% 50%;
            font-size: 14px;
            align-items: center;

            p{
                color: var(--card-font-color);
                justify-self: right;
                margin-right: 20px;
            }
            img{
                margin-left: 20px;
                @media screen and (max-height: $min-height) {
                    width: 20px;
                }
            }

            .card-header-icon{
                height: 50%;
                margin-left: 10px;
                svg{
                    height: 100%;
                }
            }

        }
        .card-content{

            background-image: url("../assets/images/central-rect.svg"); 
            background-size: cover;
            background-repeat: no-repeat;


            width: 100%;
            height: 100%;
            display: grid;
            grid-template-rows: 50% 50%;

            text-align: center;

            justify-items: center;
            align-items: center;

            color: var(--card-font-color);

            p{
                @extend .base-roboto;
                color: var(--card-font-color);
                width:80%;
            }

            .card-value{
                width: 100%;
                display: flex;
                justify-content: center;

                .bet-confirmation-bitcoin-icon{
                    margin-left: 10px;
                }

                h2{
                    @extend .base-sora;
                    color: var(--card-font-color);
                    font-size: 45px;
                    text-transform: uppercase;

                    @media screen and (max-height: $min-height) {
                        font-size: 30px;
                    }
            
                    
                }
            }

            
        }
        .card-footer{
            
            background-image: url("../assets/images/card-bottom.svg"); 
            background-size: cover;
            background-repeat: no-repeat;

            display: grid;
            grid-template-columns: 50% 50%;
            align-items: center;
            p{
                color: var(--card-font-color);
            }
            .card-data-left{
                margin-left: 20px;
                text-align: left;
            }
            .card-data-right{
                margin-right: 20px;
                text-align: right;
            }
            .card-data-bold{
                font-weight:bold;
                font-size: 18px;
            }
        }
    }

    .next-round-title{
        @extend .base-roboto;
        color: var(--card-font-color);
        /*@media screen and (min-width: $desktop-size) {
            margin-top: 30px;
            font-size: 20px;
        }*/

        
    }
    .footer{
        
        width: 90%;
        display: grid;
        grid-template-columns: 30% 40% 30%;
        
        justify-items: center;

        p{
            @extend .base-roboto;
            color: var(--card-buttons-font-color);
            height: 30px;
            font-weight: bold;
            font-size: 24px;

            cursor: pointer;
        
            border-bottom: 5px solid var(--card-font-color);;
        }
    }
}



