@import './style';

.current-price{

    width: 90%;
    margin-top: 10px;
    overflow: hidden;

    
    .price{
        display: grid;
        grid-template-rows: 80% 20%;
        color: var(--primar-font-color);

        .value{
            display: flex;

          
            .amount{
                
                font-family: Sora;
                font-style: normal;
                font-weight: bold;
                font-size: 45px;
                text-transform: uppercase;



                @media screen and (max-height: $min-height) {
                    font-size: 30px;
                }

            }

            .bitcoin-icon{
                margin-left: 20px;
                width: 60%;
                max-width: 50px;
    
                align-self: center;
                justify-self: center;

                @media screen and (max-height: $min-height) {
                    width: 35px;
                }
    
            }
        } 
    }

    .question{
        margin-top: 60px;
        font-family: Sora;
        font-style: normal;
        font-weight: 600;
        font-size: 26px;
        color: var(--secondary-font-color);

        @media screen and (max-height: $min-height) {
            margin-top: 15px;
            font-size: 22px;
        }
        

        
    }    
}



.current-price-desktop{

    overflow: hidden;

    width: 100%;
    margin-top: 0px;
    text-align: center;
    
    .price{
        display: grid;
        grid-template-rows: 80% 20%;
        color: var(--primar-font-color);

        .value{
            display: flex;
            justify-content: center;

            .amount{
                
                font-family: Sora;
                font-style: normal;
                font-weight: bold;
                text-transform: uppercase;
                font-size: 65px;
            }

            .bitcoin-icon{
                margin-left: 20px;
                width: 60%;
                max-width: 50px;
    
                align-self: center;
                justify-self: center;
            }
        } 
    }

    .question{
        font-family: Sora;
        font-style: normal;
        font-weight: 600;
        color: var(--secondary-font-color);
        margin-top: 25px;
        font-size: 36px;
        @media (max-width: 1280px) {
            margin-top: 0px;
        }
    }

    
}