@import './style';

.live-round{
    
    width: 90%;
    height: $live-round-close-height;
    

    display: flex;
    flex-direction: column;

    color: var(--live-price-font-color);


   /* @media screen and (min-width: $desktop-size) {
        width: 400px;
    }*/
    
    .live-round-header{

        width: 99%;

        /*@media screen and (min-width: $desktop-size) {
            width: 400px;
        }*/

        margin-top: 20px;
        height: 30px;
        display:flex;
        
        font-size: 12px;

        align-items: center;
        justify-content: flex-end;

        color: var(--live-price-header-font-color);

        .live-round-header-live{

            margin-right: auto;
            
            width: 25%;
            display: grid;
            grid-template-columns: 70% 30%;
            align-items: center;
            justify-items: center;
            font-weight: bold;


        }
        .live-round-header-price{
            color: var(--live-price-locked-font-color);
            .live-round-header-price-value{
                color: var(--live-price-header-font-color);
            }
        }
        .live-status-above{
            color: var(--win-color);
        }
        .live-status-bellow{
            color: var(--lose-color);
        }

        .live-round-header-your-bet-hide{
            width: 0px;
            overflow: hidden;
        }

        .live-round-header-your-bet-show{
            margin-left: 10px;
            width:30%;
            overflow:hidden;
        }
        
    }

    .live-round-details{

        @extend .base-sora;
        color: var(--live-price-font-color);

        height: 0px;
        overflow: hidden;

        .live-round-details-header{


            background: url('../assets/images/live-bg-top.svg');
            background-repeat: no-repeat;
            background-size: cover;

            height: 60px;

            display: grid;
            grid-template-columns: 50% 50%;
            align-items: center;

            img{
                margin-left: 30px;
                height: 40px;
            }
            p{
                justify-self: right;
                margin-right: 30px;
            }

        }
        .live-round-details-footer{

            margin-top: -1px;

            @extend .base-roboto;

            background: url('../assets/images/live-bg-bottom.svg');
            height: 100px;
            padding-top: 10px;

            @media screen and (max-height: $min-height) {
                height: 60px;
            }

            display: grid;
            grid-template-columns: 50% 50%;

            .live-round-details-footer-left{
                margin-left: 30px;
            }

            .live-round-details-footer-right{
                justify-self: right;
                margin-right: 30px;
                text-align: right;
            }

            .live-round-details-footer-bold{
                font-weight: bold;
            }
        }
    }

    .live-round-graphic{
        pointer-events: none;
        margin-top: 5px;

        justify-self: center;
        width: 100%;
        height: 50px;
        
        background: var(--live-round-background);
        border-radius:  15px 15px 0px 0px;

        .result{

            position: absolute;
            z-index:2;
            width: 90%;
            height: 70px;
            overflow: hidden;

            /*@media screen and (min-width: $desktop-size) {
                width: 400px;
                border-radius:  15px;
            }*/
            .result-content{

                width: 100%;
                /*@media screen and (min-width: $desktop-size) {
                    width: 400px;
                }*/

                position: absolute;
                z-index:2;

                display: grid;
                grid-template-columns: 70% 30%;

                .result-content-copy{
                    @extend .base-roboto;

                    display: grid;
                    align-items: center;
                    margin-left: 20px;

                    margin-top: 15px;
                    height: 70%;

                    .result-content-copy-header{
                        font-weight: bold;
                        font-size: 16px;
                    }

                }
                .result-content-image{
                    justify-self: right;
                }
            }

            .result-background{
                width: 90vw;
                height: 100%;
                background:white;
                border-radius:  15px;

            }
        }
    }

    .live-detail-footer{

        width: 100%;
        height: 25px;

        background: var(--live-round-background);
        border-radius:  0px 0px 15px 15px;

        display: grid;
        justify-items: center;
        align-items: center;

        .details-button{
            width: 25%;
            height: 10px;
            background: #050503;
            border-radius: 8px;

            align-self: flex-end;
            margin-bottom: 10px;

            display: grid;
            align-items: center;
            justify-items: center;

            .arrow-down-open{
                height: 80%;
                transform: rotate(180deg)
            }

            .arrow-down-close{
                height: 80%;
                transform: rotate(0deg)
            }

        }
    }  
}




